.footer {
  background-color: #f6f6f6;
  padding: 4rem 0 2rem;
  color: #4f4f4f;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 10;
  /* margin-top: 20px; */
}

.footerContent {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 2rem;
  margin-bottom: 3rem;
}

.footerSection h3 {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: #333;
}

.footerSection nav {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.footerSection nav a {
  text-decoration: none;
  color: #666;
  font-size: 0.9rem;
  transition: color 0.2s ease;
}

.footerSection nav a:hover {
  color: #7e57c2;
}

.footerBottom {
  /* border-top: 1px solid #e0e0e0; */
  padding-top: -1rem;
}

.footerBottomContent {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
  margin-bottom: 2rem;
}

.supportSection h3 {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: #333;
}

.supportSection p {
  font-size: 0.9rem;
  line-height: 1.5;
  color: #666;
  margin-bottom: 1.5rem;
}

.donateButton {
  background-color: #7e57c2;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.donateButton:hover {
  background-color: #6a3fb5;
}

.socialSection h3 {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: #333;
}

.socialLinks {
  display: flex;
  gap: 1rem;
}

.socialLinks a {
  text-decoration: none;
  font-size: 1.5rem;
  color: #666;
  transition: color 0.2s ease;
}

.socialLinks a:hover {
  color: #7e57c2;
}

.copyright {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 1rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #e0e0e0;
  font-size: 0.85rem;
  color: #666;
}

.legalLinks {
  display: flex;
  gap: 1.5rem;
}

.legalLinks a {
  text-decoration: none;
  color: #666;
  transition: color 0.2s ease;
}

.legalLinks a:hover {
  color: #7e57c2;
}

@media (max-width: 1024px) {
  .footerContent {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 768px) {
  .footerContent {
    grid-template-columns: repeat(2, 1fr);
  }

  .footerBottomContent {
    grid-template-columns: 1fr;
  }

  .copyright {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
}

@media (max-width: 480px) {
  .footerContent {
    grid-template-columns: 1fr;
  }
} 








.footer {
  padding: 2rem 0 1rem; /* Reduced from 4rem 0 2rem */
}
.footerContent {
  gap: 1rem; /* Reduced from 2rem */
  margin-bottom: 1.5rem; /* Reduced from 3rem */
}
.footerSection h3 {
  font-size: 0.9rem; /* Reduced from 1rem */
  margin-bottom: 0.5rem; /* Reduced from 1rem */
}
.footerSection nav {
  gap: 0.4rem; /* Reduced from 0.75rem */
}
.footerSection nav a {
  font-size: 0.85rem; /* Reduced from 0.9rem */
}
.footerBottom {
  padding-top: 0.5rem;
}

.footerBottomContent {
  gap: 1rem; /* Reduced from 2rem */
  margin-bottom: 1rem; /* Reduced from 2rem */
}
.supportSection p {
  font-size: 0.85rem; /* Reduced from 0.9rem */
  line-height: 1.4; /* Reduced from 1.5 */
  margin-bottom: 1rem; /* Reduced from 1.5rem */
}

.donateButton {
  padding: 0.5rem 1.25rem; /* Reduced from 0.75rem 1.5rem */
}
.copyright {
  padding: 1rem 1rem 0; /* Reduced from 2rem 1rem 0 */
  font-size: 0.8rem; /* Reduced from 0.85rem */
}

/* Add margin to the main content to prevent footer overlap */
:global(main) {
  margin-bottom: calc(100vh - 80vh); /* Adjust this value based on your footer height */
}